import { PiStarFourBold } from "react-icons/pi";
import qsalsascreenshot from "../Images/img/q-salsascreenshot.png";

export default function Qsalsa() {
    return <div className="">
      <div className="projectitle">
      <h1>Q-salsa</h1>
        <h3>
          Website voor dansschool Q-salsa-Rotterdam.
        </h3>
        </div>
         <div className="skillsbanner"> 
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Vaardigheden 
                </span>
                <span className="skillspan">
                    <PiStarFourBold />
                </span>
                <span className="skillspan">
                    Nuxt 3
                </span>
                <span className="skillspan">
                    Figma
                </span>
            </div>
          <div className="projectcontent">
            Voor de organisatie, Q-Salsa Rotterdam, heb ik een website ontwerpen en gebouwdt binnen de Nuxt 3 framework.
          <img className="projectImage" src={qsalsascreenshot} alt="screenshot of q-salsa"/>
          <h3>Links</h3>
          <ul className="callList">
                <li>
                    <h3>
                        <a className="callA" href="https://q-salsa.nl">Website</a>
                    </h3>
                </li>
            </ul>
        </div>
    </div>;
  }
  